.cartList {
  li {
    list-style: none;
    margin: 0px;
    padding: 0px;
  }

  button {
    background-color: transparent;
    border: 0;
    cursor: pointer;
    font-size: 14px;
  }

  .listDetail {
    position: relative;
    width: 100%;
    height: 118px;

    img {
      position: absolute;
      top: 18px;
      left: 18px;
      width: 100px;
      height: 100px;
    }

    .commodityTitle {
      position: absolute;
      top: 0px;
      left: 140px;
    }

    .shoppingBtn {
      position: absolute;
      top: 58px;
      left: 140px;

      button {
        width: 26px;
        height: 26px;
        background-color: #f0f0f5;
        border-radius: 50%;
        font-size: 20px;
      }
    }

    .count {
      margin: 0 20px;
    }

    .price {
      position: absolute;
      top: 0px;
      left: 360px;
      font-size: 22px;
    }
  }
}

@media (max-width: 490px) {
  .cartList {
    .listDetail {
      // border: 1px solid orange;
      .price {
        top: -18px;
        left: 280px;
      }
    }
  }
}
